'use client';

import { Prediction } from '@/types';
import { cn } from '@/lib/utils';
import { Lock } from 'lucide-react';

interface PredictionTableProps {
  predictions: Prediction[];
  isSubscribed: boolean; // Controls the blur
}

export default function PredictionTable({ predictions, isSubscribed }: PredictionTableProps) {
  return (
    <div className="overflow-hidden rounded-xl border border-gray-800 bg-black/40 backdrop-blur-sm">
      <table className="min-w-full divide-y divide-gray-800">
        <thead className="bg-gray-900/50">
          <tr>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Match</th>
            <th className="px-6 py-3 text-center text-xs font-medium text-cyan-400 uppercase tracking-wider">Strat A (Shield)</th>
            <th className="px-6 py-3 text-center text-xs font-medium text-purple-400 uppercase tracking-wider">Strat B (Sword)</th>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Rationale</th>
          </tr>
        </thead>
        <tbody className="divide-y divide-gray-800">
          {predictions.map((pred) => {
            // Logic: If not subscribed, only show 'is_free' matches. Others get blurred.
            const isLocked = !isSubscribed && !pred.is_free;

            return (
              <tr key={pred.id} className="hover:bg-white/5 transition-colors">
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="flex flex-col">
                    <span className="text-sm font-bold text-white">{pred.home_team}</span>
                    <span className="text-xs text-gray-500">vs {pred.away_team}</span>
                    <span className="text-[10px] text-gray-600 font-mono mt-1">{pred.match_time}</span>
                  </div>
                </td>

                {/* Strategy A Column */}
                <td className="px-6 py-4 whitespace-nowrap text-center relative">
                  <div className={cn("text-sm font-bold text-cyan-300", isLocked && "blur-md select-none opacity-50")}>
                    {pred.strat_a_pick}
                  </div>
                  {isLocked && (
                    <div className="absolute inset-0 flex items-center justify-center">
                      <Lock className="w-4 h-4 text-gray-600" />
                    </div>
                  )}
                </td>

                {/* Strategy B Column */}
                <td className="px-6 py-4 whitespace-nowrap text-center relative">
                  <div className={cn("text-sm font-bold text-purple-300", isLocked && "blur-md select-none opacity-50")}>
                    {pred.strat_b_pick}
                  </div>
                  {isLocked && (
                     <div className="absolute inset-0 flex items-center justify-center">
                       <Lock className="w-4 h-4 text-gray-600" />
                     </div>
                   )}
                </td>

                {/* Rationale Column */}
                <td className="px-6 py-4 relative">
                   <p className={cn("text-xs text-gray-400 max-w-xs", isLocked && "blur-sm select-none")}>
                     {pred.rationale || "Algorithmic determination based on variance models."}
                   </p>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}